@import '../_variables.scss';
.node-container {
	width: 200px;
	height: 5rem;
	padding: 2px 6px;
	border: none;
	

	p {
		height: 1.5rem;
		padding: 0;
		margin: 0;
		
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
}

.active {
	border: 2px $border-color dashed;
}

.type-date-div {
	display:flex;
	justify-content: space-between;
	padding-bottom: 5px;
}

.type {
	font-weight: 700;
}

.datetime {
	display: flex;
	align-items: center;
	font-size: .7rem;
}

.email {
	background: $email-color;
}

.meeting {
	background: $meeting-color;
}

.reminder{
	background: $reminder-color;
}

.bold {
	font-weight: 500;
}


