@import '_variables';

body{
	background:rgb(255, 226, 132);
}
.reactflow{
	top: 5vh;
	height: 95vh;
	background: $primary-background;
}

.info-slider {
	position: absolute;
	top: 20vh;
	right: 0;
	z-index: 10;

	display: flex;
	flex-direction: column;
	visibility: visible;

	width: 30vw;
	min-width: 380px;
	border: 4px dashed rgb(68, 71, 71);
	border-radius: 10px;
	margin-right: 10px;
	padding: 20px;

	&--hidden {
		visibility: hidden;
		display: none;
	}
}

.hidden-div {
	display: none;
	visibility: hidden;
}

.visible-div {
	display: flex;
	flex-direction: column;
}

.email {
	background: $email-color;
}

.meeting {
	background: $meeting-color;
}

.reminder {
	background: $reminder-color;
}

input {
	padding: 2px;
	margin-bottom: 10px;
}

.add-buttons-div {
	position:fixed;
	top: 1vh;
	display: flex;
	gap: 16px;
	z-index: 10;
	padding-left: 8px;
}

.add-button{
	outline: none;
	border: 2px dashed $border-color;
	border-radius: 2px;
	height: 30px;
	font-weight: 700;
}
